<template>
<div class="user-setting">
    <van-nav-bar title="设置" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="list-box">
        <van-cell title="修改密码" is-link @click="editPassword = true" />
        <van-cell title="注销账号" is-link @click="destroyAccount" />
    </div>
    <div class="quit-btn">
        <van-button color="#2b4868" block round @click="logout">退出登录</van-button>
    </div>
    <van-action-sheet v-model="editPassword" title="修改密码">
        <div class="content">
            <van-field v-model="oldPassword" type="text" label="旧密码：" />
            <van-field v-model="newPassword" type="text" label="新密码：" />
            <div class="confirm-btn">
                <van-button color="#2b4868" block round @click="updatePassword">确认修改</van-button>
            </div>
        </div>
    </van-action-sheet>

</div>
</template>

<script>
import {
    mapGetters
} from 'vuex'
import {
    utils
} from '../assets/js/utils'
import {
    validForm
} from '../assets/js/validForm'

export default {
    data() {
        return {
            editPassword: false,
            oldPassword: '',
            newPassword: ''
        }
    },
    methods: {
        // 退出账号
        logout() {
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })
                return;
            }

            let data = utils.queryString({
                appkey: this.appkey,
                tokenString: this.getToken
            })

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            });

            this.axios({
                    method: 'POST',
                    url: '/logout',
                    data
                })
                .then(res => {
                    this.$toast.clear();
                    console.log(res);
                    this.$notify({
                        message: res.data.msg,
                        color: '#fff',
                        background: '#2b4868'
                    });
                    if (res.data.code === 'F001') {
						this.$store.dispatch('setTokenByAction', '')
						this.$store.commit('modifyShopcartCount', '')
                        setTimeout(() => {
                            this.$router.replace({
                                name: 'Login'
                            })
                        }, 200)
                    }

                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);

                })
        },
        // 修改密码
        updatePassword() {
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })
                return;
            }

            let options = {
                oldPassword: {
                    value: this.oldPassword,
                    reg: /^[A-Za-z]\w{5,15}$/,
                    errorMsg: '密码格式不正确'
                },
                newPassword: {
                    value: this.newPassword,
                    reg: /^[A-Za-z]\w{5,15}$/,
                    errorMsg: '密码格式不正确'
                }
            }

            if (!validForm.vaild(options)) {
                return;
            }

            let data = utils.queryString({
                appkey: this.appkey,
                tokenString: this.getToken,
                password: this.newPassword,
                oldPassword: this.oldPassword
            })

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            });

            this.axios({
                    method: 'POST',
                    url: '/updatePassword',
                    data
                })
                .then(res => {
                    this.$toast.clear();
                    console.log(res);
                    this.$notify({
                        message: res.data.msg,
                        color: '#fff',
                        background: '#2b4868'
                    });
                    if (res.data.code === 'E001') {
                        this.editPassword = false;
						this.$store.dispatch('setTokenByAction', '')
						this.$store.commit('modifyShopcartCount', '')
                        setTimeout(() => {
                            this.$router.replace({
                                name: 'Login'
                            })
                        }, 200)

                    }

                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);

                })
        },
        destroyAccount() {
			if (!this.getToken) {
					this.$router.push({
						name: 'Login'
					})
					return;
				}

            this.$dialog.confirm({
                    title: '注销账号',
                    message: '是否确认注销，一旦确认无法恢复！',
                    confirmButtonColor: '#2b4868'
                })
                .then(() => {

                    
                    let data = utils.queryString({
                        appkey: this.appkey,
                        tokenString: this.getToken
                    })

                    this.$toast({
                        type: 'loading',
                        message: '加载中',
                        forbidClick: true,
                        loadingType: 'spinner',
                        duration: 0
                    });

                    this.axios({
                            method: 'POST',
                            url: '/destroyAccount',
                            data
                        })
                        .then(res => {
                            this.$toast.clear();
                            console.log(res);
                            this.$notify({
                                message: res.data.msg,
                                color: '#fff',
                                background: '#2b4868'
							});
							if(res.data.code === 'G001') {
								this.$store.dispatch('setTokenByAction', '')
								this.$store.commit('modifyShopcartCount', '')
								setTimeout(() => {
									this.$router.replace({
										name: 'Home'
									})
								},300)
							}
                            

                        })
                        .catch(err => {
                            this.$toast.clear();
                            console.log(err);

                        })
                })
                .catch(() => {

                })
        },
        onClickLeft() {
            this.$router.go(-1);
        }
    },
    computed: {
        ...mapGetters(['getToken'])
    }
}
</script>

<style lang="scss" scoped>
.user-setting {

    /deep/ .van-nav-bar__text,
    /deep/ .van-nav-bar .van-icon {
        color: #323233;
    }

    /deep/ .van-cell {
        margin: 4px 0;
    }

    .list-box {
        margin: 0 10px;
    }

    .quit-btn {
        margin: 70px 10px 0 10px;
    }

    .content {
        padding: 16px;
    }

    .confirm-btn {
        margin-top: 20px;
    }
}
</style>
